import * as React from "react";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import DeleteIcon from "@mui/icons-material/Delete";
import { Box, IconButton } from "@mui/material";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";
import "../App.css";
export default function BlogCard({
  title,
  content,
  name,
  time,
  id,
  isUser,
  isUpdated,
}) {

  const timestampFromDatabase = new Date(time); // Replace with the timestamp retrieved from the database

  const istTimestamp = timestampFromDatabase.toLocaleString('en-IN', {
    timeZone: 'Asia/Kolkata',
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
  });

  const navigate = useNavigate();
  const handleEdit = () => {
    navigate(`/secret-details/${id}`);
  };

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`/api/v1/secret/delete-secret/${id}`);
      if (data?.success) {
        toast.success("You deleted your secret");
        window.location.reload();
      }else{
        toast.error("Error in deleting your secret");
        navigate("/secret");
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Card
      sx={{
       
        width: "65%",
        height: "100%",
        margin: "auto",
        mt: 2,
        padding: 2,
        boxShadow: "5px 5px 10px #ccc",
        ":hover:": {
          boxShadow: "10px 10px 20px #ccc",
        },
      }}
    >
      {isUser && (
        <Box display={"flex"}>
          <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }}>
            <ModeEditIcon color="info" />
          </IconButton>
          <IconButton onClick={handleDelete}>
            <DeleteIcon color="error" />
          </IconButton>
        </Box>
      )}
      {(isUpdated === true) ? (
      <CardHeader
        subheader = {istTimestamp + "  (edited)"} 
      />
      ):(
        <CardHeader
        subheader = {istTimestamp}
      />
      )}
      <CardContent>
        <Typography variant="h6" color="text.secondary">
          Title : {title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Content : {content}
        </Typography>
      </CardContent>
    </Card>
  );
}
