import React, { useState, useEffect } from "react";
import axios from "axios";
import SecretCard from "../components/SecretCard";
import { Typography} from "@mui/material";
import { Grid } from '@mui/material';
const UserSecrets = () => {
  const [secrets, setSecrets] = useState([]);
  const [username, setUserName] = useState("Guest, Please login");
  //get user secrets
  const getUserSecrets = async () => {
    try {
      const id = localStorage.getItem("userId");
      const { data } = await axios.get(`/api/v1/secret/user-secret/${id}`);
      
      // const m = data.username;
      // console.log(data.username);
      setUserName(data.username);
      if (data?.success) {
        setSecrets(data?.UserSecrets.secrets.reverse());
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getUserSecrets();
  }, []);
  // console.log(secrets);
  return (
    <>
    <div style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '24px', margin: 'auto' }}>
      Username: {username}
    </div>
    <Grid container spacing={2} sx={{ width: '100%', margin: 'auto' }}>
      
      
      {secrets && secrets.length > 0 ? (
        secrets.map((secret) => (
          <Grid item xs={12} sm={6} md={4} key={secret?._id} sx={{ minHeight: '200px'}}>
          <SecretCard
            id={secret._id}
            isUser={true}
            title={secret.title}
            content={secret.content}
            username={secret.user.username}
            time={secret.createdAt}
          />
          </Grid>
        ))
      ) : (
        <Typography
            variant="h2"
            textAlign={"center"}
            fontWeight="bold"
            padding={3}
            color="gray"
          >
            You haven't created any blog
          </Typography>
      )}
      </Grid>
      </>
  );
};

export default UserSecrets;
