import React, { useState, useEffect } from "react";
import axios from "axios";
import { Grid } from '@mui/material';
import SecretCard from "../components/SecretCard";
const Secrets = () => {
  const [secrets, setSecrets] = useState([]);
  //get secrets
  const getAllSecrets = async () => {
    try {
      const { data } = await axios.get("/api/v1/secret/all-secrets");
      // console.log(data);
      if (data?.success) {
        setSecrets(data?.secrets.reverse());
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getAllSecrets();
  }, []);
  return (
    <Grid container spacing={2} sx={{ width: '100%', margin: 'auto' }}>
      {secrets &&
        secrets.map((secret) => (
          <Grid item xs={12} sm={6} md={4} key={secret?._id} sx={{ minHeight: '200px'}}>
            <SecretCard
              id={secret?._id}
              isUser={localStorage.getItem("userId") === secret?.user?._id}
              title={secret?.title}
              content={secret?.content}
              username={secret?.user?.username}
              time={secret.updatedAt}
              isUpdated={secret.isUpdated}
            />
          </Grid>
        ))}
    </Grid>
  );
};

export default Secrets;
