import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Box, Button, InputLabel, TextField, Typography , Input} from "@mui/material";
import toast from "react-hot-toast";
const CreateSecret = () => {
  const id = localStorage.getItem("userId");
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({
    title: "",
    description: "",
  });
  const handleCancle = async(e)=>{
    e.preventDefault();
    navigate("/secrets");
  }

  // input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };
  //form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/secret/create-secret", {
        title: inputs.title,
        content: inputs.content,
        user: id,
      });
      if (data.success) {
        toast.success("secret created successfully");
        navigate("/secrets");
      }else{
        toast.failure("There is an issue in creating a new secret");
        navigate("/secrets");
      }
    } catch (error) {
      toast.failure("There is an issue in creating a new secret");
      navigate("/secrets");
      console.log(error);
    }
  };
  return (
    <>
      <form onSubmit={handleSubmit}>
        <Box
          width= "65%"
          border={3}
          borderRadius={10}
          padding={3}
          margin="auto"
          boxShadow={"10px 10px 20px #ccc"}
          display="flex"
          flexDirection={"column"}
          marginTop="30px"
        >
          <Typography
            variant="h3"
            textAlign={"center"}
            fontWeight="bold"
            padding={2}
            color="gray"
          >
            Share Your Secret
          </Typography>
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: "24px", fontWeight: "bold" }}
          >
            {/* Title */}
          </InputLabel>
          <TextField
            name="title"
            value={inputs.title}
            label="Title"
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
          />
          
          <TextField
            name="content"
            value={inputs.content}
            onChange={handleChange}
            margin="normal"
            label="Content"
            multiline
            rows={5}
            variant="outlined"
            required
          />
         
          <Button width = "10%" type="submit" color="primary" variant="contained" >
            CREATE
          </Button>
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: "24px", fontWeight: "bold" }}
          >
            {/* Title */}
          </InputLabel>
          <Button type="submit" color="error" variant="contained" onClick={handleCancle} padding={3}>
            CANCLE
          </Button>
        </Box>
      </form>
    </>
  );
};

export default CreateSecret;
