import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Typography, TextField, Button } from "@mui/material";
import axios from "axios";
import { useDispatch } from "react-redux";
import { authActions } from "../redux/store";
import toast from "react-hot-toast";
import { jwtDecode } from "jwt-decode";
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
const clientId = "188183004038-94th68f8dmn8pirdu9n11cspkgibmr0r.apps.googleusercontent.com";


const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  //state
  const [inputs, setInputs] = useState({
    email: "",
    password: "",
  });
  var decoded = "" ;

  //handle input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };
  //form handle
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/user/login", {
        email: inputs.email,
        password: inputs.password,
      });
      if (data.success) {
        localStorage.setItem("userId", data?.user._id);
        dispatch(authActions.login());
        toast.success("You logged in Successfully");
        navigate("/secrets");
      }
      else{
        toast.error("Invalid Email or password");
      }
    } catch (error) {
      toast.error("Invalid Email or password");
      console.log(error);
    }
  };
  const handleSubmitByGoogle = async (credentialResponse) => {
    // credentialResponse.preventDefault();
    // setTimeout(()=>{

    // },2000);
    decoded = jwtDecode(credentialResponse.credential);
    // console.log(decoded.email);
    try {
      const { data } = await axios.post("/api/v1/user/login-by-google", {
        decoded: decoded,
      });
      console.log(data);
      if (data.success) {
        localStorage.setItem("userId", data?.user._id);
        dispatch(authActions.login());
        toast.success("You logged in Successfully");
        navigate("/secrets");
      }
      else{
        toast.error("Invalid Email or password");
      }
    } catch (error) {
      toast.error("Email or password is not correct");
      console.log(error);
    }
  };
  return (
    <>
      <form onSubmit={handleSubmit}>
        <Box
          maxWidth={450}
          display="flex"
          flexDirection={"column"}
          alignItems="center"
          justifyContent={"center"}
          margin="auto"
          marginTop={5}
          boxShadow="10px 10px 20px #ccc"
          padding={3}
          borderRadius={5}
        >
          <Typography
            variant="h4"
            sx={{ textTransform: "uppercase" }}
            padding={3}
            textAlign="center"
          >
            Login
          </Typography>

          <TextField
            placeholder="email"
            value={inputs.email}
            name="email"
            margin="normal"
            type={"email"}
            required
            onChange={handleChange}
          />
          <TextField
            placeholder="password"
            value={inputs.password}
            name="password"
            margin="normal"
            type={"password"}
            required
            onChange={handleChange}
          />

          <Button
            type="submit"
            sx={{ borderRadius: 3, marginTop: 3 }}
            variant="contained"
            color="primary"
          >
            Submit
          </Button>
          <Button
            onClick={() => navigate("/register")}
            sx={{ borderRadius: 3, marginTop: 3 }}
          >
            New user? Please Register
          </Button>
          <Button
            onClick={() => navigate("/forget-password")}
            sx={{ borderRadius: 3, marginTop: 3 }}
          >
            Forgot password?
          </Button>

          <GoogleOAuthProvider clientId= {clientId}>
          <GoogleLogin
          onSuccess={(credentialResponse) => handleSubmitByGoogle(credentialResponse)}
          // {
          //  setDecode(jwtDecode(credentialResponse.credential));
          //  console.log(decoded);
           
          // }
        // }
          onError={() => {
            console.log('Login Failed');
          }}
          />
            </GoogleOAuthProvider>;

        </Box>
      </form>
    </>
  );
};

export default Login;
