import Header from "./components/Header";
import { Routes, Route } from "react-router-dom";
import Secret from "./pages/Secrets";
import "./index.css";
import Login from "./pages/Login";
import Register from "./pages/Register";
import UserSecrets from "./pages/UserSecret";
import CreateSecret from "./pages/CreateSecret";
import SecretDetails from "./pages/SecretDetails";
import { Toaster } from "react-hot-toast";
import ForgotPassword from "./pages/forgotPassword";
function App() {
  return (
    <>
      <Header width = {"105%"}/>
      <Toaster />
      <Routes>
      <Route path="/" element={<Secret />} />
        <Route path="/secrets" element={<Secret />} />
        <Route path="/my-secret" element={<UserSecrets />} />
        <Route path="/secret-details/:id" element={<SecretDetails />} />
        <Route path="/create-secret" element={<CreateSecret className = {"createSecret"}/>} />
        <Route path="/login" element={<Login />} />
        <Route path="/forget-password" element={<ForgotPassword />} />
        <Route path="/register" element={<Register />} />
      </Routes>
    </>
  );
}

export default App;
