import React, { useState, useEffect } from "react";
import axios from "axios";
import toast from "react-hot-toast";
import { useNavigate, useParams } from "react-router-dom";
import { Box, Button, InputLabel, TextField, Typography } from "@mui/material";
const SecretDetails = () => {
  const [secrets, setSecrets] = useState({});
  const id = useParams().id;
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({});
  // get secret details
  const getSecretDetail = async () => {
    try {
      const { data } = await axios.get(`/api/v1/secret/get-secret/${id}`);
      if (data?.success) {
        setSecrets(data?.secret);
        setInputs({
          title: data?.secret.title,
          content: data?.secret.content,
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    
    getSecretDetail();
  }, [id]);

  // input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const handleCancle = async(e)=>{
    e.preventDefault();
    navigate("/secrets");
  }

  //form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(`/api/v1/secret/update-secret/${id}`, {
        title: inputs.title,
        content: inputs.content,
        isUpdated:true,
      });
      if (data?.success) {
        toast.success("Your secret updated successfully");
        navigate("/secrets");
      }
      else{
        toast.error("There is an issue while updating");
        
      }
    } catch (error) {
      console.log(error);
    }
  };
  console.log(secrets);
  return (
    <v>
      <form onSubmit={handleSubmit}>
        <Box className= "box"
          width="70%"
          border={3}
          borderRadius={10}
          padding={3}
          margin="auto"
          boxShadow={"10px 10px 20px #ccc"}
          display="flex"
          flexDirection={"column"}
          marginTop="30px"
        >
          <Typography
            variant="h3"
            textAlign={"center"}
            fontWeight="bold"
            padding={3}
            color="gray"
          >
            Edit Secret
          </Typography>
          <TextField
            name="title"
            value={inputs.title}
            placeholder = {"Title"}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
          />

          <TextField
            name="content"
            value={inputs.content}
            onChange={handleChange}
            placeholder = {"Content"}
            multiline
            rows={6}
            margin="normal"
            variant="outlined"
            required
          />
          
          <Button type="submit" color="warning" variant="contained" padding={3}>
            UPDATE
          </Button>
          <InputLabel
            sx={{ mb: 1, mt: 2, fontSize: "24px", fontWeight: "bold" }}
          >
            {/* Image URL */}
          </InputLabel>
          <Button type="submit" color="primary" variant="contained" onClick={handleCancle} padding={3}>
            CANCLE
          </Button>
        </Box>
      </form>
    </v>
  );
};

export default SecretDetails;
